@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400&display=swap");
@import "./assets/styles/normalize.scss";
@import "./assets/styles/colors.scss";
@import "./assets/styles/text.scss";

* {
  box-sizing: border-box;
}

html {
  font-family: "Jost", sans-serif;
}

a {
  text-decoration: none;
  font-size: 14px;
}

button {
  border: none;
  background-color: white;
}

// Styles for MUI Toggle Button in Toggle Group
button.MuiToggleButton-root {
  background-color: $gray !important;
  border: none !important;
}

button.MuiToggleButton-root.Mui-selected {
  background-color: $accent !important;
  border: none !important;
}
