@import "../../assets/styles/mixins.scss";
@import "../../assets/styles/colors.scss";
@import "../../assets/styles/breakpoints.scss";
@import "../../assets/styles/text.scss";

.process-container {
  max-width: 1000px;
  width: 100%;
  height: 85%;

  @media #{$mobile},  #{$mobileXL}, #{$tablet} {
    height: 100%;
  }

  .paper-content {
    padding: 2rem 2rem;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
}

.steps-container {
  height: 100%;
  margin-top: 1.8rem;
}

.mobile-step {
  height: 100%;

  .mobile-step-header {
    @include mobileStepperHeader;
    text-align: center;
    background-color: $accent;
    padding: 1rem;
  }

  .mobile-step-content {
    padding: 2rem 1rem;
    height: 100%;
  }
}
